.loader {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  margin: -25% auto 0 auto;
  border: 16px solid #f3f3f3;
  border-top: 16px solid #3498db;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}
.loader-wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
}
th span.sort-by {
  padding-right: 18px;
  width: 55%;
  margin: auto;
  display: block;
  position: relative;
}
th:last-child span.sort-by {
  padding-right: 18px;
  width: 100%;
  margin: auto;
  display: block;
  position: relative;
  text-align: left;
}
th:last-child span.sort-by:after {
  right: 25%;
}
th:last-child span.sort-by:before {
  right: 25%;
}
span.sort-by {
  display: block;
  margin-bottom: 5px;
}
span.sort-by:after {
  border: 4px solid transparent;
  content: "";
  display: block;
  height: 0;
  right: 5px;
  top: 50%;
  position: absolute;
  width: 0;
  border-top-color: #fff;
  margin-top: 1px;
}
span.sort-by:before {
  border: 4px solid transparent;
  content: "";
  display: block;
  height: 0;
  right: 5px;
  top: 50%;
  position: absolute;
  width: 0;
  border-bottom-color: #666;
  margin-top: -9px;
}
span.sort-by.up:before {
  border-bottom-color: #fff;
}
span.sort-by.up:after {
  border-top-color: #666;
}
table#grid tr {
  border-radius: 5px;
}
tr td:last-child {
  -moz-border-radius-topright: 5px;
  -moz-border-radius-bottomright: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
tr td:first-child {
  overflow: hidden;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
td:first-child:after {
  content: "";
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  transform: rotateX(-1.71887339deg) rotateY(0.5729578deg) skewX(-20deg);
  background-image: -webkit-linear-gradient(-90deg, #121623 0%, #3a4252 100%);
  margin-left: -10px;
  z-index: 0;
  border-radius: 3px;
}
td:first-child span {
  display: block;
  position: absolute;
  z-index: 999;
  width: 100%;
  text-align: center;
  margin-left: -5px;
  height: 100%;
  top: 25%;
  border-radius: 3px;
}
td:nth-child(2) span {
  position: absolute;
  left: 50%;
  margin-left: -15%;
  right: 0;
  top: 25%;
  bottom: 0;
  text-align: left;
}
tr.orangeRow td:first-child:after {
  background-image: -moz-linear-gradient(-90deg, #3a4252 0%, #ff6000 100%);
  background-image: -webkit-linear-gradient(-90deg, #3a4252 0%, #ff6000 100%);
  background-image: -ms-linear-gradient(-90deg, #3a4252 0%, #ff6000 100%);
}
tr.orangeRow td:nth-child(2) {
  color: #ff8000;
}
tr.orangeRow span {
  font-weight: bold !important;
}
